___CSS_LOADER_EXPORT___.push([module.id, ".dropzone[data-v-24d24480]{align-items:center;background-color:#fce4bb;border:2px dashed #000;display:flex;flex-direction:column;justify-content:center;row-gap:16px;transition:all .3s ease}label[data-v-24d24480]{padding:8px 12px;transition:all .3s ease}.active-dropzone[data-v-24d24480],label[data-v-24d24480]{background-color:#4bb1b1;color:#fff}.active-dropzone[data-v-24d24480]{border-color:#fff}.active-dropzone label[data-v-24d24480]{background-color:#fff;color:#4bb1b1}", "",{"version":3,"sources":["webpack://./resources/js/Components/Uploaders/VideoUpload.vue"],"names":[],"mappings":"AA8HA,2BAII,kBAAmB,CAGnB,wBAAyB,CADzB,sBAA0B,CAL1B,YAAa,CACb,qBAAsB,CACtB,sBAAuB,CAEvB,YAAa,CAGb,uBACJ,CAEA,uBACI,gBAAiB,CAGjB,uBACJ,CAEA,yDAJI,wBAAyB,CADzB,UASJ,CAJA,kCAEI,iBAEJ,CAEA,wCACI,qBAAsB,CACtB,aACJ","sourcesContent":["<template>\n    <div>\n\n        <progress v-show=\"userStore.uploadPercentage != 0\" max=\"100\" :value=\"userStore.uploadPercentage\" class=\"w-full\" />\n        <div v-show=\"userStore.uploadPercentage != 0\" class=\"w-full mb-4\">{{userStore.uploadPercentageRounded}}%</div>\n\n        <div v-show=\"uploadingMessage\" class=\"mb-4 font-bold text-center\">Please stay on this screen until upload is complete.</div>\n        <div v-show=\"uploadCompleteMessage\" class=\"mb-4 font-bold text-center\">Upload is complete. The video is now processing.</div>\n        <form v-show=\"!isHidden\" id=\"videoUploadForm\" action=\"/videoupload\" class=\"dropzone dropzoneFile border border-black rounded w-full h-48 max-w-md px-2 py-2 mb-6\">\n            <!--                            add input fields and a submit button to send data back to Laravel -->\n            <input hidden name=\"movieId\" v-model=\"form.movieId\">\n<!--            <input hidden name=\"movieTrailerId\" v-model=\"form.movieTrailerId\">-->\n            <input hidden name=\"showEpisodeId\" v-model=\"form.showEpisodeId\">\n        </form>\n\n    </div>\n</template>\n\n<script setup>\nimport { Dropzone } from \"dropzone\";\nimport { useForm } from \"@inertiajs/inertia-vue3\"\nimport { useUserStore } from \"@/Stores/UserStore\";\nimport { onMounted, ref } from \"vue\";\nimport { Inertia } from \"@inertiajs/inertia\";\n\nlet userStore = useUserStore()\nlet uploadPercentage = ref(0);\nlet uploadingMessage = ref(0);\nlet uploadCompleteMessage = ref(0);\nlet isHidden = ref(false);\n\nonMounted(() => {\n// see options for Dropzone here: https://github.com/dropzone/dropzone/blob/main/src/options.js\nlet myDropzone = new Dropzone(\"#videoUploadForm\", {\n    url: \"/videoupload\",\n    paramName: \"file\", // The name that will be used to transfer the file\n    maxFilesize: '25 GB', // MB\n    chunking: true,\n    chunkSize: 2 * 1024 * 1024,\n    parallelChunkUploads: false,\n    retryChunks: true,\n    retryChunksLimit: 10,\n    capture: null,\n    // can set the capture method as camera, microphone or video\n    // for mobile devices to skip the file selection and choose the\n    // recording device instead.\n    acceptedFiles: 'video/*, audio/*',\n    uploadprogress: function(file, progress, bytesSent) {\n        userStore.uploadPercentage = progress;\n        console.log(userStore.uploadPercentage);\n        if(userStore.uploadPercentage !== 100){\n            isHidden = true;\n        }\n    },\n    dictDefaultMessage: \"Click here or Drop video here to upload <br>(Max video file size is 25GB)\",\n    forceFallback: false, // for testing, set to true.\n    accept: function(file, done) {\n        if (file.name === \"\") {\n            done(\"Need a file.\");\n        } else if (file.size > 25000000000) {\n            console.log(file.size)\n            done(\"Video file too big.\");\n            alert('Video file must be smaller than 25GB');\n        }\n        else { done(); }\n    }\n});\n\nmyDropzone.on(\"addedfile\", file => {\n    uploadingMessage = 1;\n    console.log(`File added: ${file.name}`);\n\n});\n\nmyDropzone.on(\"complete\", function(file) {\n    uploadingMessage = 0;\n    uploadCompleteMessage = 1;\n    myDropzone.removeFile(file);\n    userStore.uploadPercentage = 0;\n    isHidden = false;\n    Inertia.reload({\n        only: [\"videos\"],\n    });\n});\n\n})\n\nlet props = defineProps({\n    movieId: Number,\n    movieTrailerId: Number,\n    showEpisodeId: Number,\n})\n\nfunction setMovieOrEpisodeId() {\n    if (props.movieId !== null) {\n        userStore.uploadMovieId = props.mov;\n    } else if (props.movieTrailerId !== null) {\n        userStore.movieTrailerId = props.movieTrailerId;\n    } else if (props.showEpisodeId !== null) {\n        userStore.uploadShowEpisodeId = props.showEpisodeId;\n    }\n}\n\nsetMovieOrEpisodeId()\n\nlet form = useForm({\n    file: [],\n    // movieId: userStore.uploadMovieId,\n    movieId: props.movieId,\n    movieTrailerId: props.movieId,\n    showEpisodeId: props.showEpisodeId,\n});\n\n// let props = defineProps({\n//     filters: Object,\n//     can: Object,\n//     videos: Object,\n//     message: String,\n//     errors: ref(''),\n//     isHidden: ref(false),\n//     done: ref(),\n// });\n\n</script>\n<style scoped>\n\n.dropzone {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    row-gap: 16px;\n    border: 2px dashed #000000;\n    background-color: #fce4bb;\n    transition: 0.3s ease all;\n}\n\nlabel {\n    padding: 8px 12px;\n    color: #fff;\n    background-color: #4bb1b1;\n    transition: 0.3s ease all;\n}\n\n.active-dropzone {\n    color: #fff;\n    border-color: #fff;\n    background-color: #4bb1b1;\n}\n\n.active-dropzone  label {\n    background-color: #fff;\n    color: #4bb1b1;\n}\n/*6b7280*/\n/*4bb1b1*/\n\n</style>\n"],"sourceRoot":""}]);
